import A from '../assets/sound/A.mp3'
import B from '../assets/sound/B.mp3'
import C from '../assets/sound/C.mp3'
import D from '../assets/sound/D.mp3'
import sectionpass from '../assets/sound/sectionpass.mp3'
import sectionfail from '../assets/sound/sectionfail.mp3'

const colorsAudio = [
  {audio: new Audio(A)},
  {audio: new Audio(B)},
  {audio: new Audio(C)},
  {audio: new Audio(D)}
]

export const colorAudioPlay = (colorClicked) => {
  colorsAudio.forEach(a=>a.audio.pause())
  colorsAudio.forEach(a=>a.audio.currentTime = 0)
  colorsAudio[colorClicked].audio.play()
}

const sectionAudio = [
  {audio: new Audio(sectionfail)},
  {audio: new Audio(sectionpass)}
]

export const sectionAudioPlay = (index) => {
  sectionAudio[index].audio.play()
}
